.button {
    padding-right: 30px;
}

button {
    width: 250px;
    padding: 10px 20px;
    border: 2px solid #EEEEEE;
    text-align: center;
    font-size: 25px;
    cursor: pointer;
    color: #EEEEEE;
    background-color: #222831;
}

button:hover {
    border-color: #00ADB5;
    color: #00ADB5;
}

@media (max-width: 800px) {
    button { 
        margin-top: 20px;
    }
}
.movie {
    margin-bottom: 70px;
    min-height: 300px;
    clear: both;
}

.movie img {
    margin: 0 30px 20px 0;
    float: left;
}

.movie h2 {
    padding-top: 10px;
    font-size: 40px;
}

.movie h3 {
    padding: 5px 0;
    font-size: 25px;
}

.movie p {
    padding-top: 5px;
    font-size: 20px;
}

@media (max-width: 800px) {

    .movie img {
        margin: 0;
        float: none;
    }
}
.searcher {
    margin: 50px 0;
}

.searcher input[type=text] {
    padding: 3px;
    font-size: 25px;
}
.app {
    margin: 0 auto;
    width: 70%;
}

.app h1 {
    margin: 40px 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 50px;
}

.app .buttons {
    display: flex;
}

@media (max-width: 800px) {
    .app {
        width: 80%;
    }

    .app .buttons {
        display: block;
    }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  color: #EEEEEE;
  background-color: #222831;
}
